button{
  background-color: #333333;
  color:#fff;
  height: 60px;
  width: 60px;
  border: 3px;
  border-color: #000;
  border-radius: 50%;
  
}
button:focus{
  outline:0px;
}
input[type=text]{
  background-color: rgb(0, 0, 0);
  font-size: 35px;
  color: #fff;
  border-color: #000;
  text-align: right;
}
input[type="text"]:disabled {
  background: #000;
}